.appWrapper {
  position: relative;
  margin: 0 auto;
  width: 1200px;
  display: grid;
  background-color: aliceblue;


  grid-template-areas:
    "header header header"
    "nav content content";

  grid-template-rows: 60px 1fr;
  grid-template-columns: 2fr 10fr;
}

.appWrapperContent {
  grid-area: content;
  border-left: 1px solid rgba(0, 128, 0, 0.13);
}

.requestError {
  z-index: 10;
  background: rgba(170, 0, 0, 0.85);
  text-align: right;
  position: absolute;
  left: 200px;
  top: 0;
  border-radius: 10px;
  padding: 3px;
}
.requestError div {
  font-size: 24px;
}

.requestError button{
  border-radius: 50%;
  outline: none;
  color: red;
  border-color: red;
}
